import React, { useState, useEffect } from 'react'
import styles from './InnerOrder.module.css';
import { Row, Col } from 'react-bootstrap'
import Chat from 'student/components/Chat/Chat'
import { useSelector } from 'react-redux'
import moment from 'moment';
import { Redirect } from 'react-router-dom';

const InnerOrder = () => {
    const { order } = useSelector(s => s.order);
    if(!order._id)return(<Redirect to='/' />);
    return (
      <div className={styles.bgLightWhite}>
        <div className={`${styles.bgSky} rounded-top`}>
          <div className="p-2 text-center">
            <h5>
              Translation Id:
              <small className="text-muted">{order.translationId}</small>
            </h5>
          </div>
          <div className="p-2 bg-white rounded-bottom">
            <div className="p-2">
              <Row>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Name :
                      <small className="text-muted">  {` ${order.userId?.name}`}</small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Email :
                      <small className="text-muted">
                          {` ${order.userId?.email}`}
                      </small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Phone :
                      <small className="text-muted">
                        {` ${order.phone}`}
                      </small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Order Deadline :
                      <small className="text-muted">
                        {` ${order.deadline
                          ? moment(order.deadline).format("DD/MM/YYYY")
                          : ""}`}
                      </small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Country :
                      <small className="text-muted">{` ${order.country}`}</small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Source Language :
                      <small className="text-muted">
                        {` ${order.sourceLanguage}`}
                      </small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      PaperLength :
                      <small className="text-muted">{` ${order.your_words}`}</small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Target Language :
                      <small className="text-muted">
                        {` ${order.targetlanguage}`}
                      </small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Notarization :
                      <small className="text-muted">{` ${order.notarization}`}</small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Certification :
                      <small className="text-muted">
                        {` ${order.certification}`}
                      </small>
                    </p>
                  </div>
                </Col>
                <Col md={4} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      Service :
                      <small className="text-muted">{` ${order.service_req}`}</small>
                    </p>
                  </div>
                </Col>
                <Col md={12} className="p-0">
                  <div
                    className={`${styles.orderText} ${styles.bgSky} rounded`}
                  >
                    <p className={styles.filedParagraph}>
                      <strong>Related Links :-</strong>
                    </p>
                    <ul>
                      <small className="text-muted">
                        {order?.files?.map((e) => (
                          <li key={e}>
                            <a href={e} target="_blank">
                              {e}
                            </a>
                          </li>
                        ))}
                      </small>
                    </ul>
                  </div>
                </Col>
              </Row>
            </div>
          </div>
        </div>
        <div className="mt-3">
          <div className={`${styles.bgSky} rounded-top`}>
            <div className="p-2 text-center">
              <h5>Questions</h5>
            </div>
          </div>
          <div className="bg-white p-2 rounded-bottom">
            <div className="p-2">
              <p className="m-0">
                <strong>Q.</strong>
                {order.message}
              </p>
            </div>
          </div>
        </div>
        <div className="mt-3">
          <div className="pr-5">
            <Chat />
          </div>
        </div>
      </div>
    );
}

export default InnerOrder
